import { transformSDKFloatToRESTFloat } from '@wix/sdk-runtime/transformations/float';
import { transformRESTFloatToSDKFloat } from '@wix/sdk-runtime/transformations/float';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import { resolveUrl } from '@wix/sdk-runtime/rest-modules';
import { ResolveUrlOpts } from '@wix/sdk-runtime/rest-modules';
import { RequestOptionsFactory } from '@wix/sdk-types';

function resolveComWixStoresCatalogProductVariantsReaderApiV3ProductVariantsReaderUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'api._api_base_domain_': [
      {
        srcPath: '/product-variants-reader',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/stores/v3/products/query-variants',
        destPath: '/v3/products/query-variants',
      },
      {
        srcPath: '/stores/v3/products/search-variants',
        destPath: '/v3/products/search-variants',
      },
    ],
    _: [
      {
        srcPath: '/_api/product-variants-reader',
        destPath: '',
      },
    ],
    '*.dev.wix-code.com': [
      {
        srcPath: '/_api/product-variants-reader',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_stores_read-only-variants-v-3';

/**
 * Retrieve product variants using structured queries with cursor-based pagination.
 *
 * Use this endpoint for simple filtering operations on `id` and `productId`.
 * For complex queries with free-text search and aggregations, use Search Variants instead.
 *
 * Up to 1,000 variants can be returned per request.
 */
export function queryVariants(payload: object): RequestOptionsFactory<any> {
  function __queryVariants({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.stores.catalog.v3.read_only_variant',
      method: 'POST' as any,
      methodFqn:
        'com.wix.stores.catalog.product.variants.reader.api.v3.ProductVariantsReader.QueryVariants',
      packageName: PACKAGE_NAME,
      url: resolveComWixStoresCatalogProductVariantsReaderApiV3ProductVariantsReaderUrl(
        { protoPath: '/v3/products/query-variants', data: payload, host }
      ),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTFloatToSDKFloat,
            paths: [
              { path: 'variants.physicalProperties.weight' },
              {
                path: 'variants.physicalProperties.pricePerUnit.settings.quantity',
              },
              { path: 'variants.revenueDetails.profitMargin' },
            ],
          },
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'variants.media.image.urlExpirationDate' },
              { path: 'variants.media.video.urlExpirationDate' },
              { path: 'variants.media.video.resolutions.urlExpirationDate' },
              {
                path: 'variants.media.video.resolutions.poster.urlExpirationDate',
              },
              { path: 'variants.media.video.posters.urlExpirationDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __queryVariants;
}

/**
 * Search product variants using free-text search with advanced query capabilities.
 *
 * Use this endpoint for complex queries that include free-text search, aggregations,
 * or when you need faceted search results. For simple filtering, use Query Variants instead.
 *
 * Supports Wix Query Language (WQL) for structured filtering combined with free-text search.
 * > **Note:** Non-visible variants, and variants related to non-visible products are filtered from results if you don't have permissions to read hidden products.
 */
export function searchVariants(payload: object): RequestOptionsFactory<any> {
  function __searchVariants({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKFloatToRESTFloat,
        paths: [
          { path: 'search.aggregations.range.buckets.from' },
          { path: 'search.aggregations.range.buckets.to' },
          {
            path: 'search.aggregations.nested.nestedAggregations.range.buckets.from',
          },
          {
            path: 'search.aggregations.nested.nestedAggregations.range.buckets.to',
          },
        ],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.stores.catalog.v3.read_only_variant',
      method: 'POST' as any,
      methodFqn:
        'com.wix.stores.catalog.product.variants.reader.api.v3.ProductVariantsReader.SearchVariants',
      packageName: PACKAGE_NAME,
      url: resolveComWixStoresCatalogProductVariantsReaderApiV3ProductVariantsReaderUrl(
        {
          protoPath: '/v3/products/search-variants',
          data: serializedData,
          host,
        }
      ),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTFloatToSDKFloat,
            paths: [
              { path: 'variants.physicalProperties.weight' },
              { path: 'aggregationData.results.ranges.results.from' },
              { path: 'aggregationData.results.ranges.results.to' },
              {
                path: 'aggregationData.results.groupedByValue.results.nestedResults.ranges.results.from',
              },
              {
                path: 'aggregationData.results.groupedByValue.results.nestedResults.ranges.results.to',
              },
              {
                path: 'aggregationData.results.nested.results.results.*.range.from',
              },
              {
                path: 'aggregationData.results.nested.results.results.*.range.to',
              },
              {
                path: 'variants.physicalProperties.pricePerUnit.settings.quantity',
              },
              { path: 'variants.revenueDetails.profitMargin' },
              { path: 'aggregationData.results.scalar.value' },
              {
                path: 'aggregationData.results.groupedByValue.results.nestedResults.scalar.value',
              },
              {
                path: 'aggregationData.results.nested.results.results.*.scalar.value',
              },
            ],
          },
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'variants.media.image.urlExpirationDate' },
              { path: 'variants.media.video.urlExpirationDate' },
              { path: 'variants.media.video.resolutions.urlExpirationDate' },
              {
                path: 'variants.media.video.resolutions.poster.urlExpirationDate',
              },
              { path: 'variants.media.video.posters.urlExpirationDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __searchVariants;
}
